
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { getTodaysWorkout, getTodaysExercises } from "@/app/actions";

const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];

export default async function WeekPlanner() {
    const userId = "557827b2-a6e2-49b9-9016-1d3bcf6a6422";


    return (
        <div className="space-y-8 p-6">
            <h1 className="text-4xl font-bold">Week Planner</h1>
            <Tabs defaultValue="Monday" className="w-full">

                {(await getTodaysWorkout('557827b2-a6e2-49b9-9016-1d3bcf6a6422')).map(async workout => (
                    <div>
                        <p key={workout.id}>{workout.name}</p>
                        {await getTodaysExercises(workout.id).then(exercises => (
                            exercises.map(exercise => (
                                <p key={exercise.id}>
                                    {exercise.name}: {exercise.weight}kg {exercise.sets} sets of {exercise.reps} reps</p>
                            ))
                        ))}
                    </div>
                ))}
            </Tabs>
        </div>
    );
}